<template>
  <div>
    <vue-good-table
      mode="remote"
      :pagination-options="{
        enabled: true,
        perPage : {perPage},
        setCurrentPage: {page}
      }"
      :search-options="{
        enabled: true,
        trigger: 'enter',
        skipDiacritics: true,
        placeholder: 'Search this table',
      }"
      :totalRows="totalRecords"
      :columns="columns"
      :rows="rows"
      v-model:isLoading="isLoading"
      v-on:page-change="onPageChange"
      v-on:sort-change="onSortChange"
      v-on:column-filter="onColumnFilter"
      v-on:per-page-change="onPerPageChange"
    >
    </vue-good-table>
  </div>
</template>

<script> 
import {mapActions, mapGetters} from 'vuex'
const userListStore = 'userListStore';

export default {
  name: "user",
  created() {
    if(this.GET_LOADED === false) {
      this.loadItems();
    }else{ 
      this.rows = this.GET_DATA;
      this.page = this.GET_PAGE;
      this.totalRecords = this.GET_TOTAL;
      this.perPage = this.GET_PERPAGE;
    }
  },
  computed: {
    ...mapGetters(userListStore, [
      'GET_PAGE',
      'GET_DATA',
      'GET_PERPAGE',
      'GET_TOTAL',
      'GET_USERLIST',
      'GET_LOADED'
    ])
  },
  data() {
    return {
      isLoading: false,
      columns: [
        {
          label: "ID",
          field: "id",
          tdClass: this.tdClassFunc,
        },
        {
          label: "Title",
          field: "title",
          tdClass: "text-center",
        },
        {
          label: "FirstName",
          field: "firstName",
          tdClass: "text-center",
        },
        {
          label: "LastName",
          field: "lastName",
          tdClass: "text-center",
        },
        {
          label: "Picture",
          field: "picture",
          tdClass: "text-center",
          width: "80px",
          html: true,
        },
      ],
      rows: [],
      totalRecords: 0,
      serverParams: {
        columnFilters: {},
        sort: {
          field: "",
          type: "",
        },
        page: 1,
        perPage: 10,
      },
    };
  },
  methods: {
    ...mapActions(userListStore, [
      'AC_SET_USERLIST',
      'AC_LOADED'
    ]),
    tdClassFunc(row) {
      if (row.field > 50) {
        return "red-class";
      }
      return "green-class";
    },
    updateParams(newProps) {
      this.serverParams = Object.assign({}, this.serverParams, newProps);
    },

    onPageChange(params) {
      this.updateParams({ page: params.currentPage });
      this.loadItems();
    },

    onPerPageChange(params) { 
      this.updateParams({ perPage: params.currentPerPage });
      this.loadItems();
    },

    onSortChange(params) {
      this.updateParams({
        sort: [
          {
            type: params.sortType,
            field: this.columns[params.columnIndex].field,
          },
        ],
      });
      this.loadItems();
    },
    onSearch(params) {
      // params.searchTerm - term being searched for
      // params.rowCount - number of rows that match search
      console.log(params);
    },
    onColumnFilter(params) {
      this.updateParams(params);
      this.loadItems();
    },

    // load items is what brings back the rows from server
    loadItems() {
      // getFromServer(this.serverParams).then((response) => {
      //   this.totalRecords = response.totalRecords;
      //   this.rows = response.rows;
      // });
      console.log("loadItems");
      this.axios
        .get("/user?limit=20", {
          params: {
            page: this.page,
            limite: this.perPage,
          },
          headers: {
            "Content-Type": "application/json",
            "app-id": "620f10044e0af2ed3075fe3d",
          },
        })
        .then((res) => {
          this.AC_LOADED({loaded: true})
          this.totalRecords = res.data.total;
          this.rows = res.data.data;
          this.rows = this.rows.map((item) => ({
            ...item,
            picture: `<img width="80" height="80" src="${item.picture}" />`,
          }));
          this.AC_SET_USERLIST({page:this.page, total:res.data.total, perPage: this.perPage, data: this.rows})
          console.log(this.rows);
        })
        .catch((e) => {
          console.log("ERROR: ", e);
        });
    },
  },
};
</script>

