<template>
  <div id="q-app" class="LoginBox-">
    <q-layout view="lHh Lpr fff">
      <q-page
        class="window-height window-width row justify-center items-center"
        style="background: linear-gradient(#8274c5, #5a4a9f)"
      >
        <div class="column q-pa-lg">
          <div class="row">
            <q-card
              square
              class="shadow-24"
              style="width: 400px; height: 540px"
            >
              <q-card-section class="bg-deep-purple-7">
                <h4 class="text-h5 text-white q-my-md">{{ title }}</h4>
              </q-card-section>
              <q-card-section>
                <q-fab
                  color="primary"
                  @click="switchTypeForm"
                  icon="add"
                  class="absolute"
                  style="top: 0; right: 12px; transform: translateY(-50%)"
                >
                  <q-tooltip>New User Registration</q-tooltip>
                </q-fab>
                <q-form class="q-px-sm q-pt-xl">
                  <q-input
                    ref="username"
                    square
                    clearable
                    v-model="username"
                    lazy-rules
                    :rules="[this.required, this.short]"
                    type="username"
                    label="User"
                  >
                    <template v-slot:prepend>
                      <q-icon name="person" />
                    </template>
                  </q-input>
                  <q-input
                    ref="email"
                    v-if="register"
                    square
                    clearable
                    v-model="email"
                    type="email"
                    lazy-rules
                    :roules="[this.required, this.isEmail, this.short]"
                    label="Email"
                  >
                    <template v-slot:prepend>
                      <q-icon name="email" />
                    </template>
                  </q-input>
                  <q-input
                    ref="password"
                    square
                    clearable
                    v-model="password"
                    :type="passwordFieldType"
                    lazy-rules
                    :rules="[this.required, this.short]"
                    label="Password"
                  >
                    <template v-slot:prepend>
                      <q-icon name="lock" />
                    </template>
                    <template v-slot:append>
                      <q-icon
                        :name="visibilityIcon"
                        @click="switchVisibility"
                        class="cursor-pointer"
                      />
                    </template>
                  </q-input>
                  <q-input
                    ref="repassword"
                    v-if="register"
                    square
                    clearable
                    v-model="repassword"
                    :type="passwordFieldType"
                    lazy-rules
                    :rules="[this.required, this.short, this.diffPassword]"
                    label="RePassword"
                  >
                    <template v-slot:prepend>
                      <q-icon name="lock" />
                    </template>
                    <template v-slot:append>
                      <q-icon
                        :name="visibilityIcon"
                        @click="switchVisibility"
                        class="cursor-pointer"
                      />
                    </template>
                  </q-input>
                </q-form>
              </q-card-section>
              <q-card-actions class="q-px-lg">
                <q-btn
                  unelevated
                  size="lg"
                  color="secondary"
                  @click="submit"
                  class="full-width text-white"
                  :label="btnLabel"
                />
              </q-card-actions>
              <q-card-section v-if="!register" class="text-center q-pa-sm">
                <p class="text-grey-6">비밀번호를 잊어 버렸습니까?</p>
              </q-card-section>
            </q-card>
          </div>
        </div>
      </q-page>
      <!-- q info -->
      <q-btn label="version" color="primary" @click="showAlert(0)" />
      <q-btn label="platform" color="primary" @click="showAlert(1)" />
      <q-btn label="screen" color="primary" @click="showAlert(2)" />
      <q-btn label="lang" color="primary" @click="showAlert(3)" />
      <q-btn label="iconSet" color="primary" @click="showAlert(4)" />
      <q-btn label="cordova" color="primary" @click="showAlert(5)" />
      <q-btn label="capacitor" color="primary" @click="showAlert(6)" />
      <q-dialog v-model="alert">
        <q-card>
          <q-card-section>
            <div class="text-h6">Alert</div>
          </q-card-section>

          <q-card-section class="q-pt-none">
            <p>{{ alertText }}</p>
          </q-card-section>

          <q-card-actions align="right">
            <q-btn flat label="OK" color="primary" v-close-popup />
          </q-card-actions>
        </q-card>
      </q-dialog>
    </q-layout>
  </div>
</template>

<script>
// import { inject, toRefs } from "vue";
export default {
  name: "LoginBox",
  props: {
    msg: String,
  },
  data() {
    return {
      title: "로그인",
      email: "",
      username: "",
      password: "",
      repassword: "",
      register: false,
      passwordFieldType: "password",
      btnLabel: "Login",
      visibility: false,
      visibilityIcon: "visibility",
      alert: false,
      alertText: "",
    };
  },
  computed: {},
  methods: {
    showAlert(type) {
      switch (type) {
        case 0: {
          this.alertText = this.$q.version;
          this.alert = true;
          return;
        }
        case 1: {
          this.alertText = this.$q.platform;
          this.alert = true;
          return;
        }
        case 2: {
          this.alertText = this.$q.screen;
          this.alert = true;
          return;
        }
        case 3: {
          this.alertText = this.$q.lang;
          this.alert = true;
          return;
        }
        case 4: {
          this.alertText = this.$q.iconSet;
          this.alert = true;
          return;
        }
        case 5: {
          this.alertText = this.$q.cordova;
          this.alert = true;
          return;
        }
        case 6: {
          this.alertText = this.$q.capacitor;
          this.alert = true;
          return;
        }
      }
    },
    required(val) {
      return (val && val.length > 0) || "필드를 채워주세요";
    },
    diffPassword(val) {
      const val2 = this.$refs.password.value;
      return (val && val === val2) || "비밀번호가 일치하지 않습니다.";
    },
    short(val) {
      return (val && val.length > 3) || "3자 이상 입력해주세요";
    },
    isEmail(val) {
      const emailPattern =
        /^(?=[a-zA-Z0-9@._%+-]{6,254}$)[a-zA-Z0-9._%+-]{1,64}@(?:[a-zA-Z0-9-]{1,63}\.){1,8}[a-zA-Z]{2,63}$/;
      return emailPattern.test(val) || "이메일 형식이 아닙니다.";
    },
    submit() {
      if (this.register) {
        this.$refs.email.validate();
        this.$refs.username.validate();
        this.$refs.password.validate();
        this.$refs.repassword.validate();
      } else {
        this.$refs.email.validate();
        this.$refs.password.validate();
      }

      if (!this.register)
        if (!this.$refs.email.hasError && !this.$refs.password.hasError) {
          this.$q.notify({
            icon: "done",
            color: "positive",
            message: "Авторизация",
          });
        }
    },
    switchTypeForm() {
      this.register = !this.register;
      this.title = this.register ? "회원 가입" : "로그인";
      this.btnLabel = this.register ? "회원 가입" : "로그인";
    },
    switchVisibility() {
      this.visibility = !this.visibility;
      this.passwordFieldType = this.visibility ? "text" : "password";
      this.visibilityIcon = this.visibility ? "visibility_off" : "visibility";
    },
  },
};
</script>

<style scoped>
.LoginBox {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
}
</style>
